@import "../Participant";
.participant-card {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 2rem 2rem 1rem 2rem;
  width: 100%;
  border: 1px solid #000000;
  &__avatar {
    text-align: center;
    height: 5rem;
    img {
      height: 5rem;
    }
  }
  &__name {
    text-align: center;
    font-size: 3rem;
    word-break: break-all;
  }
}
